body {
  text-align: center;
  background-color: #f2f2f2;
  font-family: Arial, sans-serif;
}

.breed-select {
  width: 300px;
  margin: 20px;
  padding: 10px;
  font-size: 1rem;
}

.loader, .error {
  text-align: center;
  font-size: 1.2rem;
  display: none;
}

.cat-info {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  margin-top: 20px;
  padding: 20px;
  box-shadow: 0 0 10px #0003;
}

.cat-image {
  max-width: 100%;
  max-height: 600px;
  border: 1px solid #ccc;
}

#cat-name {
  margin-top: 10px;
  font-size: 1.5rem;
}

#cat-description, #cat-temperament {
  margin-top: 10px;
  font-size: 1rem;
}

.loader, .breed-select, .cat-info, .error {
  display: none;
}

